import {React, useEffect, useState} from 'react'

import './TokenSelectorView'

import etherService from '../../services/EtherService'
import toastService from '../../services/ToastService'
import walletService from '../../services/WalletService'
import {listToMap, mapToList} from '../../utils/TokenUtils'
import {isEmptyTransfer, isInsufficientBalance} from '../../utils/ValidationUtils'
import ConnectWallet from '../ConnectWallet'
import TokenTag from '../TokenTag'

import '../../styles/common.css'
import '../TokenAmount.css'
import './BatchTransferView.css'

function BatchTransferView({tokens, onAddTokens}) {
	const [senderWallet, setSenderWallet] = useState('')
	const [selectedTokens, setSelectedTokens] = useState(tokens)
	const [recipientAddress, setRecipientAddress] = useState('0x4c4153a6f4B83a549437CeeE41d3d317C5Eff6Dc')
	const [isProcessingTx, setProcessingTx] = useState(false)

	const removeSelected = (token) => {
		setSelectedTokens((prevTokens) => {
			const updatedTokens = {...prevTokens}
			delete updatedTokens[token.symbol]

			return updatedTokens
		})
	}

	const changeAmountToTransfer = (token, amount) => {
		setSelectedTokens((prevTokens) => {
			const updatedTokens = {
				...prevTokens,
				[token.symbol]: {...token, amountToTransfer: amount}
			}

			return updatedTokens
		})
	}

	const send = async () => {
		for (const symbol in selectedTokens) {
			const token = selectedTokens[symbol]
			if (isInsufficientBalance(token)) {
				toastService.error(`Insufficient Balance for ${symbol}!`)
				return
			}
		}

		const tokensList = mapToList(selectedTokens)
			.filter(token => token.amountToTransfer > 0)

		setProcessingTx(true)
		etherService
			.batchTransfer(senderWallet, tokensList, recipientAddress)
			.then(result => {
				console.log(`Transfer result: ${result}`)
				setProcessingTx(false)
				toastService.success('Transfer processed successfully!')
			})
			.catch(error => {
				setProcessingTx(false)
				toastService.error(`Error during Transfer processing: ${error}!`)
			})
	}

	useEffect(() => {
		walletService.onChange(() => {
			walletService
				.getSelectedWallet()
				.then(wallet => setSenderWallet(wallet))
		})

		walletService
			.getSelectedWallet()
			.then(wallet => setSenderWallet(wallet))
	}, [])

	useEffect(() => {
		const load = async () => {
			const senderWallet = await walletService.getSelectedWallet()
			if (!senderWallet) {
				return
			}

			const tokenBalanceRequests = mapToList(selectedTokens)
				.map(token => {
					return etherService
						.getBalance(token.address, senderWallet)
						.then(availableBalance => {
							return {...token, availableBalance: availableBalance}
						})
				})
			const tokens = await Promise.all(tokenBalanceRequests)

			setSelectedTokens(listToMap(tokens, (token) => token.symbol))
		}

		load()
	}, [senderWallet, isProcessingTx])

	return (
		<div className={'popup-window neon-shadow'}>
			<div className={'block'}>
				<div className={'block-title'}>You Send</div>
				{
					Object.keys(selectedTokens)
						.map(symbol => selectedTokens[symbol])
						.map(token => (
							<div key={token.symbol}
								className={'token-amount-wrapper ' + (!!isInsufficientBalance(token) && 'error-block')}>
								<div className={'token-amount'} key={token.symbol}>
									<TokenTag token={token} onClick={removeSelected}/>
									<input value={token.amountToTransfer}
										onChange={e => changeAmountToTransfer(token, e.target.value)}
										placeholder={'0'}
										className={'token-amount-input'} type="text"/>
								</div>
								<div className={'available-balance-wrapper'}>
									<div className={'available-balance-value'}><b>Max: </b>{token.availableBalance}
									</div>
								</div>
							</div>
						))
				}
				<button className={'btn'} onClick={() => onAddTokens(selectedTokens)} disabled={isProcessingTx}>
                    Add tokens
				</button>
			</div>
			<div className={'block'}>
				<div className={'block-title'}>From</div>
				<div style={{color: 'var(--text-second'}}>{senderWallet}</div>
			</div>
			<div className={'block'}>
				<div className={'block-title'}>To Recipient</div>
				<input value={recipientAddress} onChange={e => setRecipientAddress(e.target.value)}
					className={'input-field'} type="text" placeholder="Recepient's Address..."/>
				<div className={'tax'}>Tax: 1% + 0.005 ETH</div>
			</div>
			<div className={'block'} style={{marginTop: 'auto'}}>
				{!!senderWallet && (
					<button className={'btn'}
						onClick={send}
						disabled={isProcessingTx || isEmptyTransfer(selectedTokens)}>Send</button>
				)}
				{!senderWallet && (
					<ConnectWallet/>
				)}
			</div>
		</div>
	)
}

export default BatchTransferView