const handlers = []

const getSelectedWallet = async () => {
	return window.ethereum
		.request({method: 'eth_accounts'})
		.then(accounts => accounts[0])
}

const isConnected = async () => {
	if (!window.ethereum) {
		return false
	}

	return getSelectedWallet()
		.then(wallets => wallets && wallets.length !== 0)
}

const connectWallet = () => {
	return window.ethereum
		.request({method: 'eth_requestAccounts'})
		.then(() => {
			const connectedAddress = getSelectedWallet()
			console.log(`Connected to wallet with address: ${connectedAddress}`)

			return connectedAddress
		})
}

const onChange = (handler) => {
	handlers.push(handler)
}

if (!isConnected() && localStorage.getItem('connectedAddress')) {
	await connectWallet()
}

window.ethereum.on('accountsChanged', () => {
	for (const handler of handlers) {
		handler()
	}
})

export default {getSelectedWallet, isConnected, connectWallet, onChange}
