import {Web3} from 'web3'

const TOKEN_MIN_ABI = require('../abi/TokenMinABI.json')
const BATCH_TRANSFER_ABI = require('../abi/BatchTransferABI.json')
const BATCH_TRANSFER_ADDRESS = process.env.REACT_APP_BATCH_TRANSFER_ADDRESS
const ETH_ADDRESS = process.env.REACT_APP_ETH_ADDRESS

const web3 = new Web3(window.ethereum)

const getBalance = async (tokenAddress, walletAddress) => {
	if (tokenAddress !== ETH_ADDRESS) {
		return getTokenBalance(tokenAddress, walletAddress)
	}

	return getWalletBalance(walletAddress)
}

const getWalletBalance = async (walletAddress) => {
	return web3.eth
		.getBalance(walletAddress)
		.then(balance => Number(balance) / Math.pow(10, 18))
}

const getTokenBalance = async (tokenAddress, walletAddress) => {
	const tokenContract = new web3.eth.Contract(TOKEN_MIN_ABI.abi, tokenAddress)

	return tokenContract.methods
		.balanceOf(walletAddress).call()
		.then(balance => Number(balance) / Math.pow(10, 18))
}

const batchTransfer = async (sender, tokens, receiver) => {
	const tokensAddresses = tokens
		.map(token => token.address)
	const amounts = tokens
		.map(token => token.amountToTransfer)
		.map(amount => web3.utils.toWei(amount, 'ether'))

	console.log(`Send to: ${receiver}`)
	console.log(tokensAddresses)
	console.log(amounts)

	const approveRequests = []
	for (let i = 0; i < tokensAddresses.length; i++) {
		const address = tokensAddresses[i]
		const amount = amounts[i]
		const request = approveTransfer(sender, address, BATCH_TRANSFER_ADDRESS, amount)
		approveRequests.push(request)
	}
	await Promise.all(approveRequests)

	const batchTransferContract = new web3.eth.Contract(BATCH_TRANSFER_ABI.abi, BATCH_TRANSFER_ADDRESS)
	const response = await batchTransferContract.methods
		.batchTransfer(tokensAddresses, amounts, receiver)
		.call()
	console.log(response)
}

const approveTransfer = async (sender, tokenAddress, transferContractAddress, amountToSend) => {
	const tokenContract = new web3.eth.Contract(TOKEN_MIN_ABI, tokenAddress)

	return  tokenContract.methods
		.approve(transferContractAddress, amountToSend).send({from: sender})
}

export default {getBalance, batchTransfer}