import {React} from 'react'

import './TokenListItem.css'

function TokenListItem({token, onClick, style}) {
	return (
		<div onClick={() => onClick(token)} style={style} className={'token-list-item'}>
			<img className={'token-list-item-logo'} src={token.logoURI} alt={token.name}/>
			<div>
				<div className={'token-list-item-name'}>{token.name}</div>
				<div className={'token-list-item-symbol'}>{token.symbol}</div>
			</div>
		</div>
	)
}

export default TokenListItem