import {React, useEffect, useState} from 'react'

import toastService from '../services/ToastService'
import walletService from '../services/WalletService'

import './ConnectWallet.css'

function ConnectWallet({style}) {
	const [isConnected, setConnected] = useState()

	const initConnectFlow = () => {
		walletService
			.connectWallet()
			.then(() => {
				setConnected(walletService.isConnected)
				toastService.success('Wallet connected')
			})
			.catch(e => toastService.error(e.message || e))
	}

	useEffect(() => {
		walletService.onChange(() =>  {
			walletService
				.isConnected()
				.then(result => setConnected(result))
		})

		walletService
			.isConnected()
			.then(result => setConnected(result))
	}, [])

	return (
		<div className={'connect-wallet-wrapper'} style={style}>
			{!isConnected && (
				<button onClick={initConnectFlow} className={'btn connect-wallet-btn'}>Connect Wallet</button>
			)}
		</div>
	)
}

export default ConnectWallet