export const sortByName = (t1, t2) => t1.name.localeCompare(t2.name)

export const listToMap = (list, keyFunction) => {
	return list
		.reduce((map, obj) => {
			const key = keyFunction(obj)
			map[key] = obj
			return map
		}, {})
}

export const mapToList = (map) => {
	return Object.keys(map).map(key => map[key])
}