import {React} from 'react'

import './TokenTag.css'

function TokenTag({token, onClick}) {
	return (
		<div className={'tag-wrapper'}>
			<img className={'tag-logo'} src={token.logoURI} alt={token.name}/>
			<div className={'tag-title'}>{token.symbol}</div>
			{!!onClick && (
				<div onClick={() => onClick(token)} className={'tag-remove-btn'}></div>
			)}
		</div>
	)
}

export default TokenTag