import React, {useState} from 'react'
import {ToastContainer} from 'react-toastify'

import ConnectWallet from './components/ConnectWallet'
import BatchTransferView from './components/views/BatchTransferView.js'
import TokenSelectorView from './components/views/TokenSelectorView.js'
import tokenService from './services/TokenService'
import {listToMap} from './utils/TokenUtils'

import './App.css'
import 'react-toastify/dist/ReactToastify.css'

function App() {
	const [isSelectTokens, setSelectTokens] = useState(false)
	const [selectedTokens, setSelectedTokens] = useState(listToMap(tokenService.getDefaultTokens(), token => token.symbol))

	const onConfirmSelect = (selectedTokens) => {
		setSelectedTokens(selectedTokens)
		setSelectTokens(false)
	}

	const onAddTokens = (selectedTokens) => {
		setSelectedTokens(selectedTokens)
		setSelectTokens(true)
	}

	return (
		<div className={'in-root'}>
			<div className={'header-wrapper'}>
				<img className={'header-logo'} src="/logo.png" alt="Logo"/>
				<div className={'header-title'}>atcher</div>
				<ConnectWallet style={{marginRight: '20px', marginTop: '20px'}}/>
			</div>
			<div className={'content'}>
				{!isSelectTokens && <BatchTransferView tokens={selectedTokens} onAddTokens={onAddTokens}/>}
				{!!isSelectTokens &&
					<TokenSelectorView preSelectedTokens={selectedTokens} onConfirmSelect={onConfirmSelect}/>}
			</div>
			<ToastContainer/>
		</div>
	)
}

export default App