import React, {useEffect, useState} from 'react'
import {List} from 'react-virtualized'

import tokenService from '../../services/TokenService'
import {sortByName} from '../../utils/TokenUtils'
import TokenListItem from '../TokenListItem'
import TokenTag from '../TokenTag'

import './TokenSelectorView.css'

function TokenSelectorView({preSelectedTokens, onConfirmSelect}) {
	const [tokens, setTokens] = useState([])
	const [selected, setSelected] = useState(preSelectedTokens)
	const [searchQuery, setSearchQuery] = useState('')

	const addSelected = (token) => {
		setSelected((prevSelected) => {
			const selected = {...prevSelected}
			selected[token.symbol] = token

			return selected
		})
	}

	const removeSelected = (token) => {
		setSelected((prevSelected) => {
			const selected = {...prevSelected}
			delete selected[token.symbol]

			return selected
		})
	}

	const filteredTokens = () => {
		return tokens
			.filter(token => selected[token.symbol] === undefined)
			.filter(token =>
				token.symbol.toLocaleLowerCase().indexOf(searchQuery.toLocaleLowerCase()) !== -1
                || token.address.toLocaleLowerCase().indexOf(searchQuery.toLocaleLowerCase()) !== -1
			)
	}

	useEffect(() => {
		tokenService
			.getAllTokens()
			.then(tokens => {
				const sorted = tokens.sort(sortByName)
				setTokens(sorted)
			})
	}, [])

	return (
		<div className={'popup-window neon-shadow'}>
			<div className={'popup-window-title'}>Select Tokens</div>
			<div className={'search-wrapper'}>
				<input value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)}
					className={'search-field input-field'} type="text"
					placeholder="Search token by Name or paste Address"/>
			</div>
			<div className={'selected-list-title'}>Tokens</div>
			<div className={'selected-list'}>{
				Object.keys(selected)
					.map(symbol => selected[symbol])
					.map(token => (
						<TokenTag key={token.symbol} token={token} onClick={removeSelected}/>
					)
					)
			}</div>
			<List
				width={400}
				height={5 * 50}
				rowHeight={55}
				rowRenderer={({index, key, style}) => (
					<TokenListItem key={key} token={filteredTokens()[index]} onClick={addSelected} style={style}/>
				)}
				rowCount={filteredTokens().length}
				overscanRowCount={10}/>
			<button onClick={() => onConfirmSelect(selected)} className={'btn'}>Confirm</button>
		</div>
	)
}

export default TokenSelectorView